table {
    width: 100%;
    border-collapse: collapse;
    font-family: 'Segoe UI', sans-serif;
}

th {
    background-color: #f0f0f0;
    border-bottom: 2px solid #ddd;
    padding: 10px;
    text-align: left;
    font-weight: bold;
}

td {
    border-bottom: 1px solid #eee;
    padding: 8px;
}

tr:hover {
    background-color: #f9f9f9; /* Petit effet au survol */
}